<script>

export default {
    //
    data() {
		return {
            pageFlag : '',
			tableName: '',
			registerForm: {
                xingbie: '',
                daoshixingming: '',
                xingbie: '',
            },
			rules: {},
            yanjiushengxingbieOptions: [],
            yanjiushengdaoshigonghaoOptions: [],
            daoshixingbieOptions: [],
		}
    },
	mounted() {
	},
    created() {
      this.pageFlag = this.$route.query.pageFlag;
      this.tableName = this.$route.query.role;
      if ('yanjiusheng' == this.tableName) {
        this.rules.yanjiushengzhanghao = [{ required: true, message: '', trigger: 'blur' }];
      }
      if ('yanjiusheng' == this.tableName) {
        this.rules.yanjiushengxingming = [{ required: true, message: '', trigger: 'blur' }];
      }
      if ('yanjiusheng' == this.tableName) {
        this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
      }
        this.yanjiushengxingbieOptions = ",".split(',');
      if ('yanjiusheng' == this.tableName) {
        this.rules.nianling = [{ required: true, validator: this.$validate.isIntNumer, trigger: 'blur' }];
      }
      if ('yanjiusheng' == this.tableName) {
        this.rules.dianhuahaoma = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
      }
        this.$http({
          url: `option/daoshi/daoshigonghao`,
          method: "get",
          params: {}
        }).then(({ data }) => {
          if (data && data.code === 0) {
              this.yanjiushengdaoshigonghaoOptions = data.data;
          }
        });
      if ('daoshi' == this.tableName) {
        this.rules.daoshigonghao = [{ required: true, message: '', trigger: 'blur' }];
      }
      if ('daoshi' == this.tableName) {
        this.rules.daoshixingming = [{ required: true, message: '', trigger: 'blur' }];
      }
      if ('daoshi' == this.tableName) {
        this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
      }
        this.daoshixingbieOptions = ",".split(',');
      if ('daoshi' == this.tableName) {
        this.rules.dianhuahaoma = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
      }
    },
    //
    methods: {
      // uuid
      getUUID () {
        return new Date().getTime();
      },
        // 
      yanjiushengtouxiangUploadChange(fileUrls) {
          this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
        yanjiushengdaoshigonghaoChange () {
          this.$http({
            url: `follow/daoshi/daoshigonghao?columnValue=`+ this.registerForm.daoshigonghao,
            method: "get",
            params: {}
          }).then(({ data }) => {
            if (data && data.code === 0) {
            if(data.data.daoshixingming){
              this.registerForm.daoshixingming = data.data.daoshixingming
            }
            }
          });
        },
      daoshitouxiangUploadChange(fileUrls) {
          this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },

        // 


      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var url=this.tableName+"/register";
               if(`yanjiusheng` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
                this.$message.error(``);
                return
               }
               if(`daoshi` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
                this.$message.error(``);
                return
               }
            this.$http.post(url, this.registerForm).then(res => {
              if (res.data.code === 0) {
                this.$message({
                  message: '',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.$router.push('/login');
                  }
                });
              } else {
                this.$message.error(res.data.msg);
              }
            });
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

